body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Existing CSS styles */
.App {
  margin: 20px;
}

.form-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  flex: 1 1 150px;
}

.label-input {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
}

input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Button styling */
button {
  margin: 0 5px;
  padding: 6px 12px; /* Adjusted padding for smaller buttons */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px; /* Adjusted font size for smaller buttons */
}

button.btn-primary {
  background-color: #007bff;
  color: #fff;
}

button.btn-danger {
  background-color: #dc3545;
  color: #fff;
}

button.btn-primary:hover,
button.btn-danger:hover {
  opacity: 0.9;
}

